import react, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
    padding : 20px;
`;

let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.색상}
`;

function Detail(props) {

    let [alert, alert변경] = useState(true);
    // let [inputDate, inputData변경] = useState('');

    useEffect(() => {
        let 타이머 = setTimeout(() => {
           alert변경(false); 
        }, 2000);
        console.log('안녕');
        return () => { clearTimeout(타이머) }
    },[alert]);



    let { id } = useParams();
    let history = useHistory();
    var foundIdx = 0;


    props.shoes.forEach(element => {
        if (element.id == id) {
            foundIdx = parseInt(id);
        }
    });

    return (
        <div className="container">
            <박스>
                <제목 className="red">Detail</제목>
            </박스>
            
            {/* { inputDate }
            <input onChange={(e) => { inputData변경(e.target.value) }} /> */}

            {
                alert === true
                    ? (<div className="my-alert2">
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>)
                    : null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src={`https://codingapple1.github.io/shop/shoes${foundIdx + 1}.jpg`} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{props.shoes[foundIdx].title}</h4>
                    <p>{props.shoes[foundIdx].content}</p>
                    <p>{props.shoes[foundIdx].price}</p>

                    <Info 재고={props.재고} ></Info>

                    {/* 알트쉬프트다운화살표 누르면 아래에 하나더 복사할 수 있음 */}
                    <button className="btn btn-danger">주문하기</button>
                    <button className="btn btn-primary" onClick={() => {
                        history.push('/');
                    }}>뒤로가기</button>
                </div>
            </div>
        </div>
    )
}

function Info(props){
    return (
        <p>재고 : {props.재고[1]}</p>
    )
}

export default Detail;